.hotspot {
  position: absolute;
}

.hotspot-container {
  position: relative;
}
.hotspot-container img {
  width: 100%;
  height: auto;
}

.hotspot-content {
  overflow: hidden;
  top: 2.75rem;
  min-width: 15.625rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background-color: #007bff;
  opacity: 0.9;
  color: #fff;
  border-radius: 2%;
  padding: 0 0.625rem;
}

.hotspot-trigger {
  text-align: center;
  display: block;
  width: 2.125rem;
  height: 2.125rem;
  margin-bottom: 0.625rem;
  border-radius: 100%;
  line-height: 2.0625rem;
  background: #007bff;
  color: #fff;
  font-size: 1.875rem;
  font-weight: 400;
  position: relative;
  z-index: 10;
  cursor: pointer;
  transform: rotateZ(0);
  transition: ease-out 0.15s transform;
}
.toggled .hotspot-trigger {
  transform: rotateZ(-45deg);
}
.hotspot-trigger:focus {
  outline: 0;
}
.hotspot-trigger:before {
  content: "";
  position: absolute;
  top: -0.3125rem;
  left: -0.3125rem;
  z-index: -1;
  width: 2.375rem;
  height: 2.375rem;
  border: 0.1875rem solid #007bff;
  border-radius: 50%;
  animation: pulsate 1.5s infinite;
}

@keyframes pulsate {
  0% {
    transform: scale(1, 1);
    opacity: 1;
  }
  100% {
    transform: scale(1.2, 1.2);
    opacity: 0;
  }
}